/**
 * Global CSS Rules
 */

 *, *:before, *:after {
 	box-sizing: border-box;
 }

body.md-default-theme, 
body, 
html.md-default-theme, 
html {
	background: $white;
}

body {
	margin: 0;
	padding: 0;
	font-family: $bodyFont;
	color: $grey;
	font-size: 16px;
	overflow-x: hidden;
}

/**
 * Headings
 */
h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: $mainFont;
	color: $black;
	padding: 0;
}

p {
	margin: 0 0 20px 0;
	line-height: 1.4;
	
	&:last-child {
		margin-bottom: 0;
	}
	
}

/**
 * Icons
 */
.icon {
	display: inline-block;
	font-family: $iconFont;
}

/**
 * Buttons
 * Edits the style of the Material Design Buttons.
 */
.md-button,
.md-button.md-default-theme.md-primary.md-fab,
.md-button.md-primary.md-fab,
.md-button.md-default-theme.md-primary.md-raised,
.md-button.md-primary.md-raised {

	&.button {
		display: inline-block;
		padding: 5px 20px;
		margin: 0;
		font-size: 18px;
		font-family: $mainFont;
		text-decoration: none;
		text-transform: none;
		font-weight: 700;
		border: 0;
		transition: all ease 0.5s;

		&:hover {
			cursor: pointer;
		}

		&.orange {
			background: $orange !important;
			color: #fff;

			&:hover, &:active {
				background: lighten($orange, 5%) !important;
			}
		}

		&.link {
			font-weight: 300;
			text-transform: uppercase;
			color: $darkBlue;
			box-shadow: none;

			&:hover, &:active {
				background: rgba(0,0,0,0.06);
			}


		}

		&.green {
			background: $green;
			color: #fff;

			&:hover {
				background: lighten($green, 5%);
			}
		}

		&.blue {
			background: $lightBlue;
			color: #fff;

			&:hover {
				background: lighten($lightBlue, 5%);
			}
		}
		
		&.dark-blue {
			background: $darkBlue;
			color: #fff;

			&:hover {
				background: lighten($darkBlue, 5%);
			}
		}
		
		&.line {
			background: none !important;
			border: 2px solid;
			padding-top: 3px;
			padding-bottom: 3px;
				
			&.white {
				border-color: $white;
				color: $white;
				
				&:hover {
					background: $white !important;
					color: $orange;
				}
			}
			
			&.orange {
				border-color: $orange;
				color: $orange;
				
				&:hover {
					background: $orange !important;
					color: $white;
				}
			}
			
		}
		
		&.large {
			font-size: 20px;
			padding: 5px 40px
		}
		
	}
}

/**
 * Global Layouts
 */
.container {
	position: relative;
	max-width: 1000px;
	width: 100%;
	margin: 0 auto;
}

/**
 * Forms
 */
.field {
	position: relative;
	width: 100%;
	text-align: left;

	.note {
		text-align: center;
		padding: 5px 0;
		font-size: 12px;
	}

	md-input-container {
		margin: 10px 0 0;

		&.md-input-invalid {

			label {
				color: $red;
			}

			.md-input {
				border-color: $red;
			}
		}

		&.md-input-has-value {
			md-input-container.md-default-theme:not(.md-input-invalid).md-input-has-value label,
			md-input-container:not(.md-input-invalid).md-input-has-value label {
				color: #fff;
			}
		}

		&.md-input-focused {

			.md-input {
				border-color: #fff;
			}

			.md-text {
				color: #fff;
			}

			.md-placeholder,
			label {
				color: #fff;
			}

			md-select {
				border-color: #fff;
				color: #fff;

				&.md-default-theme .md-select-value.md-select-placeholder,
				.md-select-value.md-select-placeholder {
					color: #fff;
					font-family: $mainFont;
				}

				&.md-default-theme .md-select-value,
				.md-select-value {
					border-color: #fff;
				}

				&.md-default-theme .md-select-icon,
				.md-select-icon {
					color: #fff;
				}
			}
		}

		&:not(.md-input-focused):not(.md-input-invalid) md-select.md-default-theme .md-select-value span:first-child:after,
		&:not(.md-input-focused):not(.md-input-invalid) md-select .md-select-value span:first-child:after {
			color: #fff;
		}

		&.md-default-theme:not(.md-input-focused):not(.md-input-invalid) label.md-required:after,
		&:not(.md-input-focused):not(.md-input-invalid) label.md-required:after {
			color: #fff;
		}

		.md-input {
			border-color: #fff;
		}

		.md-placeholder {
			color: #fff;
		}

		label {
			color: #fff;
			font-family: $mainFont;

			&.md-required {
				&:after {
						color: #fff;
				}
			}
		}
	}

	md-checkbox {

		.md-container {
			top: 0;
			transform: translateY(0);
			-webkit-transform: translateY(0);
		}

		&.md-checked {
			.md-icon {
				background: $orange;
			}
		}
		.md-label {
			color: #fff;
			font-family: $mainFont;
			vertical-align: top;

			a {
				color: $darkerBlue;
			}
		}

		&.md-default-theme:not(.md-checked) .md-icon,
		&:not(.md-checked) .md-icon {
			border-color: #fff;
		}

	}

	/*
	&.double {

		@include mdBreakpoint(gtSm) {
			width: 49%;
			display: inline-block;
		}
	}

	input {
		width: 100%;
		display: block;
		padding: 10px;
		border-radius: 5px;
		border: 1px solid $grey;
	}

	label {
		color: $grey;
		font-family: $mainFont;
		display: block;
		position: absolute;
		top: 10px;
		left: 10px;
	}
	*/

	&.checkbox {
		padding: 0 10px;
		margin: 10px 0;

		input {
			display: block;
			position: absolute;
			width: auto;
			left: 0;
			top: 0;

		}
		label {
			position: relative;
			display: inline-block;
			vertical-align: top;
			padding-left: 25px;
			padding-right: 15px;
			color: #fff;


			a {
				color: #fff;
				font-weight: bold;
			}
		}
	}

	&.actions {
		button {
			display: block;
			margin-top: 30px;
			width: 100%;
		}
	}

	&.select {
		label {
			position: static;
			display: block;
			color: $black;
			font-weight: 700;
			margin-bottom: 10px;
		}

		select {
			display: block;
			width: 100%;
			font-family: $mainFont;
		}
	}
}

/**
 * Select Dropdown of Material Design
 */
md-select {
	border-color: #fff;
	font-family: $mainFont;
	color: #fff;

	&.md-default-theme .md-select-value.md-select-placeholder,
	.md-select-value.md-select-placeholder {
		color: #fff;
		font-family: $mainFont;
	}

	&.md-default-theme .md-select-value,
	.md-select-value {
		border-color: #fff;
	}

	&.md-default-theme .md-select-icon,
	.md-select-icon {
		color: #fff;
	}
}

md-select.md-default-theme.ng-invalid.ng-touched .md-select-value,
md-select.ng-invalid.ng-touched .md-select-value {
	color: $red !important;
	border-color: $red !important;
}

md-input-container md-select.md-default-theme .md-select-value span:first-child:after,
md-input-container md-select .md-select-value span:first-child:after {
	color: $red;
}

md-option .md-text {
	font-family: $mainFont;
}


/**
 * Bottom Buttons
 */
.actions-extra {
	color: #fff;
	text-align: center;
	text-decoration: none;
	display: block;
	margin: 20px auto 0;
	font-family: $mainFont;
}

/**
 * Actions below every section
 */
.meta-actions {
	margin-top: 20px;

	.button {
		display: block;
		text-align: center;
		max-width: 350px;
		margin: 0 auto;
	}
}

/* Steps Style */
.steps {
	position: relative;
	margin-top: 20px;

	.step {
		font-family: $bodyFont;
		line-height: 22px;
		text-align: center;
		position: relative;
		padding: 20px 0 0;
		clear: both;

		@include mdBreakpoint(gtXs) {
			display: inline-block;
			vertical-align: top;
			margin: 0 2% 20px;
			max-width: 42%;
			padding: 0;
		}
		
		@include mdBreakpoint(gtSm) {
			max-width: 28%;
		}

		.step-num {
			color: $lightBlue;
			border: 10px solid $lightBlue;
			border-radius: 50%;
			text-align: center;
			font-size: 36px;
			font-family: $mainFont;
			font-weight: 700;
			width: 70px;
			height: 70px;
			line-height: 50px;
			margin: 0 auto;
			float: left;

			@include mdBreakpoint(gtXs) {
				float: none;
			}
		}

		.step-desc {
			padding-left: 90px;

			@include mdBreakpoint(gtXs) {
				padding-left: 0;
			}

			p {
				margin: 20px 0 0;
				padding: 0;
				line-height: 22px;
			}

		}
	}
}

.actions {
	text-align: center;

	.button {
		display: block;
		width: 100%;

		@include mdBreakpoint(gtXs) {
			display: inline-block;
			width: 350px;
		}
	}

}


