		.button {
			display: block;
			text-align: center;
		}
	}
	
}

$pagination_color: #5CACEE;

.pn-pagination {
	color: $pagination_color;
	padding: 20px 0;
	text-align: right;
	
	a {
		color: inherit;
		text-decoration: none;
	}
	
	a,
	span {
		border: 1px solid $pagination_color;
		display: inline-block;
		vertical-align: middle;
		padding: 9px;
		margin: 0 5px;
		-webkit-border-radius: 3px;
		-moz-border-radius: 3px;
		border-radius: 3px;
	}
	
	a:hover {
		background: $pagination_color;
		color: $white;
	}
	
	.prev-page {
		margin-right: 5px;
	}
	
	.next-page {
		margin-left: 5px;
	}
	
	.md-button.button.blue, 
	.md-button.md-default-theme.md-primary.md-fab.button.blue, 
	.md-button.md-primary.md-fab.button.blue, 
	.md-button.md-default-theme.md-primary.md-raised.button.blue, 
	.md-button.md-primary.md-raised.button.blue {
		border: 1px solid $pagination_color;
		padding: 10px;
		line-height: 1;
		height: auto;
		min-height: 0;
		font-size: inherit;
		
		&.disabled {
			background: none;
			color: $pagination_color;
			-webkit-box-shadow: none;
			-moz-box-shadow: none;
			box-shadow: none;
		}
	}
	
}

table {
	background: $table-bg;
	color: $white;
	font-size: 14px;
	text-align: left;
	width: 100%;
	border-collapse: collapse;
	
	@include mdBreakpoint(gtXs) {
		font-size: 16px;
	}
	
	th {
		background: $table-th;
		padding: 15px 5px;
		text-align: center;
		
		@include mdBreakpoint(gtXs) {
			padding: 15px 0;
		}
		
	}
	
	td {
		font-weight: bold;
		padding: 10px 20px;
		border: 1px solid rgba(55, 120, 173,0.2);
	}
	
}

.results-footer {
	font-size: 14px;
	line-height: 1.4;
	padding: 15px 20px 30px;
	
	@include mdBreakpoint(gtSm) {
		display: table-cell;
		height: 100%;
		text-align: left;
	}
	
	.col {
		margin-bottom: 10px;
		
		@include mdBreakpoint(gtSm) {
			display: table-cell;
			height: 100%;
		}
		
		&.first {
			
			@include mdBreakpoint(gtSm) {
				padding-right: 20px;
				width: 40%;
			}
			
		}
		
	}
	
	& + .layout-row .button {
		width: 100%;
	}
	
}

.page-template-tpl-about  .about-main-content {
	
	.content {
		@include mdBreakpoint(gtXs) {
			padding-left: 4%;
		}
	}
	
}

.social-icons {
	list-style: none;
	margin: -10px 0 10px -10px;
	overflow: hidden;
	padding: 0;
	text-align: center;
	
	li {
		display: inline;
	}
	
	a {
		display: inline-block;
		height: 40px;
		width: 40px;
		text-indent: -9999px;
		position: relative;
		color: #fff;
		font-size: 20px;
		opacity: 0.5;
		
		&:before {
			position: absolute;
			left: 50%;
			top: 50%;
			text-indent: 0;
			margin: -10px;
		}
		
		&:hover {
			opacity: 1;
		}
		
	}
	
}

body.page-template-tpl-product {
	
	#Hero {
		background: #3978AD;
	}

	.steps {
		text-align: center;
	}

	.step {
		display: inline-block;
		
		img {
			max-width: 50%;
			height: auto;
		}
		
		p {
			text-align: left;
		}
		
	}

	.section-actions {
		.button {
			width: 100%;
			@include mdBreakpoint(gtSm) {
				width: 350px;
			}
		}
	}
	
	.shadow-box {
		@include mdBreakpoint(gtSm) {
			margin-left: -20px;
			margin-right: -20px;
		}
	}

	
}

.shadow-box {
	
	img {
		width: 100%;
		height: auto;
		display: block;
		border: 10px solid #fff;
		-webkit-box-shadow: 0 0 14px rgba(0,0,0,0.2);
		-moz-box-shadow: 0 0 14px rgba(0,0,0,0.2);
		box-shadow: 0 0 14px rgba(0,0,0,0.2);
	}
	
}